import GraphinaApexChartBase from './GraphinaApexChartBase';

// Child class specifically for Heatmap Charts
export default class HeatmapChart extends GraphinaApexChartBase {
    constructor() {
        super();
        this.observer = null; // For IntersectionObserver
    }

    // Setup handlers for heatmap chart type
    setUpChartsHandler() {
        this.chartHandlers = {
            heatmap: (element) => this.observeChartElement(element, 'heatmap'),
        };
    }

    // Setup IntersectionObserver to call setupChart when the element is in the viewport
    observeChartElement(element, chartType) {
        if(gcfe_public_localize.view_port === 'off'){
            if (!this.observer) {
                this.observer = new IntersectionObserver((entries) => {
                    entries.forEach((entry) => {
                        if (entry.isIntersecting) {
                            // Element is in viewport; initialize the chart
                            this.setupChart(jQuery(entry.target), chartType);
                            // Stop observing the element after initializing the chart
                            this.observer.unobserve(entry.target);
                        }
                    });
                }, { threshold: 0.1 }); // Trigger when at least 10% of the element is visible
            }
            this.observer.observe(element[0]); // Start observing the chart element
        }else{
            this.setupChart(element,chartType);
        }
    }

    getChartOptions(finalChartOptions, chartType,extraData,responsive_options,elementId) {
        if (chartType === 'heatmap') {
            finalChartOptions.responsive = responsive_options
        }
        return finalChartOptions;
    }
}

// Initialize HeatmapChart
new HeatmapChart();
